    const [isNight, setIsNight] = useState(false);
    const [open, setOpen] = useState(false);
    const [tabValue, setTabValue] = useState(0);
    const [swipeIndex, setSwipeIndex] = useState(0);

    const handleClickOpen = tabIndex => {
        setTabValue(tabIndex);
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    const onSwipeHandler = index => {
        setSwipeIndex(index);
    };

    const handleTabsChange = (event, newValue) => {
        setTabValue(newValue);
    };

    const currentDayBuildingImage = useMemo(() => {
        if (reserveCount >= 15000) return images.dayBuilding5;
        else if (reserveCount >= 10000) return images.dayBuilding4;
        else if (reserveCount >= 6000) return images.dayBuilding3;
        else if (reserveCount >= 3000) return images.dayBuilding2;
        else if (reserveCount >= 1000) return images.dayBuilding1;
        else return images.dayBuilding0;
    }, [reserveCount]);

    const isShowSpotLight = useMemo(() => {
        const reservationCountMapping = {
            0: 1000,
            1: 3000,
            2: 6000,
            3: 10000,
            4: 15000
        };

        return reserveCount >= reservationCountMapping[swipeIndex];
    }, [reserveCount, swipeIndex]);

