import React, { useState } from 'react';
/** @jsx jsx */
import { jsx, css } from '@emotion/react';
import Dialog from '@material-ui/core/Dialog';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';

import { images } from '../constants';

const styles = theme => ({
    button: {
        backgroundColor: '#ffffff',
        color: '#333333',
        '&:hover': {
            backgroundColor: '#fffff'
        }
    },
    child: {
        backgroundColor: '#eaae9b'
    },
    rippleVisible: {
        opacity: 0.8,
        animation: `$enter 550ms ${theme.transitions.easing.easeInOut}`
    },
    '@keyframes enter': {
        '0%': {
            transform: 'scale(0)',
            opacity: 0.1
        },
        '100%': {
            transform: 'scale(1)',
            opacity: 0.8
        }
    }
});

function MyButton({ classes, ...other }) {
    const { button: buttonClass, ...rippleClasses } = classes;

    return (
        <Button
            TouchRippleProps={{ classes: rippleClasses }}
            className={buttonClass}
            {...other}
        />
    );
}

export default function InitialDialog() {
    const [isMetionDialogOpen, setIsMetionDialogOpen] = useState(true);
    const MyBtn = withStyles(styles)(MyButton);

    return (
        <Dialog open={isMetionDialogOpen}>
            <div
                css={css`
                    display: flex;
                    color: rgb(209, 183, 176);
                    background-color: rgb(55, 58, 71);
                    height: 100%;
                    max-width: 320px;
                    padding: 16px;
                    flex-direction: column;
                `}
            >
                <img
                    css={css`
                        max-width: 100px;
                        margin: auto;
                    `}
                    src={images.logo}
                    alt="chane交友軟體 logo"
                />
                <div>
                    <Typography variant="h5">[通知]</Typography>
                    <Typography variant="subtitle1">
                        由於疫情嚴峻，
                        本產品所有活動與上架時間將延後，視疫情情況開放，敬請期待。
                    </Typography>
                    <Typography variant="subtitle1">疫情期間，</Typography>
                    <Typography variant="subtitle1">非必要勿出門，</Typography>
                    <Typography variant="subtitle1">出門請戴口罩，</Typography>
                    <Typography variant="subtitle1">切勿在外飲食。</Typography>
                    <Typography variant="subtitle1">
                        chance.圈圈 開發團隊關心您。
                    </Typography>
                    <br />
                    <Typography variant="subtitle2">
                        更多防疫相關資訊:{' '}
                        <a
                            css={css`
                                color: rgb(209, 183, 176);
                            `}
                            href="https://www.cdc.gov.tw/"
                            target="_blank"
                        >
                            衛生福利部疾病管制署
                        </a>
                    </Typography>
                </div>
            </div>
            <Button
                color="secondary"
                onClick={() => setIsMetionDialogOpen(false)}
            >
                好的，我知道了
            </Button>
        </Dialog>
    );
}
